import React from 'react'
import { Link } from 'react-router-dom'
function Header() {
    return (
        <header id="header">
            <div className="container main-menu">
                {/* <div className="row align-items-center justify-content-between d-flex"> */}
                    {/* <div id="logo">
                        <Link to="./"><img src="img/logo.png" alt="" className="logo" /></Link>
                    </div> */}
                    <nav className="navbar navbar-expand-md navbar-light row align-items-center justify-content-between d-flex">
                        <Link to="./"><img src="img/logo.png" alt="" className="logo" /></Link>
                        <button type="button" className="navbar-toggler bg-light" data-toggle="collapse" data-target="#nav">
                            <span className="navbar-toggler-icon" />
                        </button>
                        <div className="collapse navbar-collapse collapse justify-content-between" id="nav">
                            <ul className="navbar-nav ml-auto">
                                <li className="nav-item">
                                    <Link className="nav-link text-dark font-weight-bold px-3"  to="/">Home</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link text-dark font-weight-bold px-3" to="/about">About</Link>
                                </li>
                                <li className="nav-item">
                                <Link className="nav-link text-dark font-weight-bold px-3" to="/resume">Resume</Link>
                                </li>
                                <li className="nav-item">
                                <Link className="nav-link text-dark font-weight-bold px-3" to="/portfolio">Portfolio</Link>
                                </li>
                                <li className="nav-item">
                                <Link className="nav-link text-dark font-weight-bold px-3" to="/contact">Contact</Link>
                                </li>
                            </ul>
                        </div>
                    </nav>


                    {/* <nav id="nav-menu-container">
                        <ul className="nav-menu">
                            <li><Link to="/">Home</Link></li>
                            <li><Link to="/about">About</Link></li>
                            <li><Link to="/resume">Resume</Link></li>
                            <li><Link to="/portfolio">Portfolio</Link></li>
                            <li><Link to="/contact">Contact</Link></li>
                        </ul>
                    </nav> */}
                {/* </div> */}
            </div>
        </header>
    )
}

export default Header