import React, { Component } from 'react'
import { Card } from 'react-bootstrap';
import Parser from 'html-react-parser';
import loadjs from 'loadjs';

export class About extends Component {
    componentWillMount() {
        loadjs('/js/main.js', () => { });
    }
    render() {
        return (
            <section className="home-about-area pt-120">
                <div className="container">
                    <h4 style={{ paddingBottom: '20px',fontWeight:"700" }}>What I Do?</h4>
                    <div className="row align-items-center justify-content-between card-deck">
                      
                        {this.props.about
                            .map((detail, index) =>

                            <div className="col-sm-6 py-2" key={index}>
                                <Card style={{ width: '100%' }} className="card-about h-100 mb-4">
                                    <Card.Body className="service txt-center">
                                        <Card.Img className="service__icon" src={detail.imageUrl} alt=""></Card.Img>
                                        <Card.Subtitle className="mb-2 text-title">{detail.title}</Card.Subtitle>
                                        {/* {detail.description} */}
                                        <Card.Text>
                                        {Parser(detail.description)}

                                            {/* parse({detail.description}); */}

                                            {/* {detail.description} */}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
                                </div>
                            )}
                        {/* <Card style={{ width: '18rem' }} className="card-about">
                        <Card.Body className="service txt-center">
                            <Card.Img className="service__icon" src="img/about-img.png" alt=""></Card.Img>
                            <Card.Subtitle className="mb-2 text-muted">Web Design</Card.Subtitle>
                            <Card.Text>
                                Some quick example text to build on the card title and make up the bulk of
                                the card's content.
                         </Card.Text>
                        </Card.Body>
                    </Card> */}

                    </div>
                </div>
            </section>

        )
    }
}
export default About